@font-face {
  font-family: 'YS Text';
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  src: url(//yastatic.net/s3/home/fonts/ys/1/text-regular.woff2) format('woff2'),
    url(//yastatic.net/s3/home/fonts/ys/1/text-regular.woff) format('woff');
}

@font-face {
  font-family: 'YS Text';
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  src: url(//yastatic.net/s3/home/fonts/ys/1/text-medium.woff2) format('woff2'),
    url(//yastatic.net/s3/home/fonts/ys/1/text-medium.woff) format('woff');
}

@font-face {
  font-family: 'YS Text';
  font-weight: 700;
  font-style: normal;
  font-stretch: normal;
  src: url(//yastatic.net/s3/home/fonts/ys/1/text-bold.woff2) format('woff2'),
    url(//yastatic.net/s3/home/fonts/ys/1/text-bold.woff) format('woff');
}

html {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  flex-grow: 1;

  margin: 0;

  font-family: 'YS Text', 'Helvetica Neue', Arial, sans-serif;
  -webkit-font-feature-settings: 'liga', 'kern';
  -moz-font-feature-settings: 'liga', 'kern';
  font-feature-settings: 'liga', 'kern';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-size: 13px;
  line-height: 18px;

  overflow-y: scroll;
}

.body_modal {
  position: fixed;
  left: 0;
  right: 0;
  height: 100%;
}

#root {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
